/*! Dev by tactic.team */
html {
  --doc-padding: 40px;
  --doc-font-size: 14px;
  --cols: 12;
  --gutter: 20px;
  --column: calc( ( 100% - ( var(--gutter) * (var(--cols) - 1) ) ) / 12 );
  --w-content: calc(1420px + var(--doc-padding)*2);
  --font-family: "Inter", sans-serif;
  --white: #fff;
  --black: #282827;
  --gray: #747473;
  --gray-2: #929292;
  --gray-3: #d4d4d4;
  --line-light: #f5f5f5;
  --bg-light: #fafafa;
  --bg-light-2: #f9f9f9;
  --yellow-main: #f8ca02;
  --yellow-secondary: #ffdf65;
  --orange: #ffc165;
  --orange-2: #f1711c;
  --green: #56e264;
  --toner: rgba(0, 0, 0, 0.25);
  --toner-160: rgba(0, 0, 0, 0.16);
  --pro-grey-pro: #a8a8a8;
  --pro-pro-lines: #232322;
  --pro-lines-bg-pro: #151515;
  --successful: #49aa53;
}
@media (max-width: 640px) {
  html {
    --doc-padding: 20px;
  }
}

/* inter-regular - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter-v18-cyrillic_latin-regular.woff2") format("woff2"); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* inter-900 - cyrillic_latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/inter-v18-cyrillic_latin-900.woff2") format("woff2"); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
body, html {
  background: var(--white);
  color: var(--black);
  font-family: var(--font-family);
  font-size: var(--doc-font-size);
  line-height: 1.4;
  font-weight: 400;
  margin: 0 auto;
  -webkit-text-size-adjust: 100%;
}

body {
  font-size: var(--doc-font-size);
}

* {
  box-sizing: border-box;
}

h1:first-child, h2:first-child, h3:first-child, h4:first-child, h5:first-child, h6:first-child, p:first-child, ul:first-child, ol:first-child, li:first-child, blockquote:first-child, tt:first-child, mark:first-child,
form:first-child, table:first-child, a:first-child, .button:first-child {
  margin-top: 0 !important;
}
h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child, ul:last-child, ol:last-child, li:last-child, blockquote:last-child, tt:last-child, mark:last-child,
form:last-child, table:last-child, a:last-child, .button:last-child {
  margin-bottom: 0 !important;
}

/*! Dev by tactic.team */
.overflow {
  overflow: hidden;
}

.hide, .hidden {
  display: none !important;
}

.container {
  max-width: 100%;
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}

section, .section, article, header, footer {
  width: 100%;
  clear: both;
  position: relative;
  z-index: 2;
}

.content, .wrap {
  width: 100%;
  max-width: var(--w-content);
  margin: 0 auto;
  padding: 0 var(--doc-padding);
  position: relative;
  z-index: 2;
  box-sizing: border-box;
}

.flex {
  display: flex;
  flex-flow: row wrap;
}
.flex.row {
  flex-flow: row wrap;
}
.flex.row > * {
  margin-top: 0;
  flex: 1 1 auto;
}
.flex.col {
  flex-flow: column wrap;
}
.flex.col > * {
  margin-top: 0;
  flex: 1 1 auto;
}

.img {
  display: flex;
  overflow: hidden;
  position: relative;
  background: var(--bg-light-2);
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: var(--bg-light);
  overflow: hidden;
}
.bg img, .bg video, .bg iframe {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
  z-index: 1;
}

@media (min-width: calc(640px + 1px)) {
  .mob-only {
    display: none !important;
  }
}

@media (max-width: 640px) {
  .tab-only {
    display: none !important;
  }
}
@media (min-width: calc(1200px + 1px)) {
  .tab-only {
    display: none !important;
  }
}

@media (min-width: calc(1200px + 1px)) {
  .mob-and-tab-only {
    display: none !important;
  }
}

@media (max-width: 640px) {
  .tab-and-desktop-only {
    display: none !important;
  }
}

@media (max-width: 1200px) {
  .desktop-only {
    display: none !important;
  }
}

@media (min-width: 1921px) {
  .before-uhd-only {
    display: none !important;
  }
}

@media (max-width: 1920px) {
  .uhd-only {
    display: none !important;
  }
}

.button {
  position: relative;
  display: inline-flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.button[disabled] {
  filter: grayscale(100%);
  cursor: default;
}